<script>
import { mapState } from "vuex";
import { empty } from "../../common";
export default {
  data() {
    return {
      inputHelper: "",
      lightBoxFile: [],
      deletingId: 0,
      deleting: false,
      index: null,
      filters: {
        query: "",
        order_by: "-updated_at",
        pageSize: 50,
        page: 1,
        date: this.$moment().format("DD/MM/YYYY"),
      },
      loading: false,
      actions: [],
      sortList: [
        {
          name: "Created (oldest first)",
          value: "created_at",
        },
        {
          name: "Created (newest first)",
          value: "-created_at",
        },
        {
          name: "Updated (oldest first)",
          value: "updated_at",
        },
        {
          name: "Updated (newest first)",
          value: "-updated_at",
        },
        {
          name: "Customer A–Z",
          value: "customer__name",
        },
        {
          name: "Customer Z–A",
          value: "-customer__name",
        },
      ],
      loadingDelivey: false,
      deliveries: [],
      selectedDate: null,
      selectedDelivery: null,
      allCheck: false,
      gettingOrder: false,
      page: 1,
      max: 5,
      selected: [],
      isReadyToGet: false,
      deliveryHistories: {
        next: 1,
        results: [],
        total_pages: 0,
      },
      scroll: 0,
    };
  },
  components: { empty },
  computed: {
    ...mapState(["isReady", "store"]),
  },
  created() {
    this.$store.commit("setIsReady", false);
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  mounted() {
    this.filters = {
      query: this.$route.query.query || "",
      order_by: this.$route.query.order_by || "-updated_at",
      pageSize: this.$route.query.page_size || "50",
      page: this.$route.query.page || "1",
      date: this.$route.query.date || this.$moment().format("DD/MM/YYYY"),
    };
    (this.inputHelper = this.$route.query.query || ""),
      (this.isReadyToGet = true);
    this.getdeliveryHistories();
  },
  watch: {
    filters: {
      handler: function (value) {
        if (this.isReadyToGet) {
          this.$router
            .push({
              path: "/delivery-history",
              query: {
                query: value.query,
                page_size: value.pageSize,
                page: value.page,
                order_by: value.order_by,
                date: value.date,
              },
            })
            .catch(() => {});
        }
      },
      deep: true,
    },
  },
  methods: {
    handleScroll(e) {
      const scrollHeight =
        e.target.scrollingElement.scrollHeight -
        e.target.scrollingElement.clientHeight * 2;
      if (scrollHeight < e.target.scrollingElement.scrollTop) {
        if (!this.loading) {
          this.getdeliveryHistories();
        }
      }
    },
    openLightBox(image) {
      this.lightBoxFile = [image];
      this.index = 0;
    },
    updateStatus() {
      // if (com == "Delete") {
      //   this.deletedeliveryHistories();
      // }
    },
    deleteDelivery(id) {
      this.deletingId = id;
      this.deleting = true;

      var url = `delivery-history/?id=` + id;
      this.$axios
        .delete(url)
        .then(() => {
          this.filters = {
            query: "",
            order_by: "-updated_at",
            pageSize: "50",
            page: "1",
            date: this.$moment().format("DD/MM/YYYY"),
          };
          this.deleting = false;
          this.$noti.display({
            message: "Success!",
            status: "success",
            color: "success",
          });
        })
        .catch((err) => {
          this.deleting = false;
          this.loading = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    deletedeliveryHistories() {
      this.loading = true;
      var data = [];
      this.selected.forEach((ele) => {
        data.push(ele.id);
      });
      var url = `deliveryHistories/delete/`;
      this.$axios
        .post(url, { data: data })
        .then(() => {
          this.getdeliveryHistories();
        })
        .catch((err) => {
          this.loading = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    setFilterQuery() {
      this.filters.query = this.inputHelper;
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.selected = val;
    },

    getdeliveryHistories() {
      if (this.deliveryHistories.next) {
        var date = this.$moment(this.filters.date, "DD/MM/YYYY").format(
          "YYYY-MM-DD"
        );
        this.loading = true;
        this.gettingOrder = true;
        var url = `delivery-history-list/?date=${date}&page=${this.deliveryHistories.next}&page_size=${this.filters.pageSize}&query=${this.filters.query}&order_by=${this.filters.order_by}`;
        this.$axios
          .get(url)
          .then((response) => {
            this.gettingOrder = false;
            const scroll = window.scrollY;
            this.deliveryHistories = {
              ...response.data,
              results: [
                ...this.deliveryHistories.results,
                ...response.data.results,
              ],
            };
            setTimeout(() => {
              window.scroll(0, scroll);
            }, 50);
            setTimeout(() => {
              this.loading = false;
            }, 100);

            this.$store.commit("setIsReady", true);
          })
          .catch((err) => {
            this.loading = false;
            this.gettingOrder = false;
            this.$noti.display({
              message: err,
              status: "error",
              color: "error",
            });
          });
      }
    },
  },
};
</script>

